import {
  createTheme,
  ThemeProvider,
  StyledEngineProvider,
  styled,
  ButtonGroup,
  Button,
  Typography,
  Box,
} from "@mui/material";
import { BrowserRouter, Switch, Route, Link } from "react-router-dom";

import { orange, yellow } from "@mui/material/colors";
import { MUI_PCS } from "./MUI501-props-changes-style/MUI_PCS";
import { GitHub } from "@mui/icons-material";

function App() {
  const theme = createTheme({
    palette: {
      mode: "dark",
      primary: { main: yellow["A700"] },
      secondary: { main: orange["A700"] },
    },
  });

  const AppWarp = styled("div")({
    backgroundColor: "#080808",
    minHeight: "100vh",
    maxWidth: "100vw",
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    // fontSize: "calc(10px + 2vmin)",
    color: theme.palette.text.primary,
  });

  const Header = styled(Box)({
    width: "100%",
    display: "flex",
    justifyContent: "space-around",
  });

  const Footer = styled(Box)({
    width: "100%",
    minHeight: "6rem",
  });

  function handleSourceCodeClick() {
    console.log("window.location : ", window.location);
    let url;
    switch (window.location.pathname) {
      case "/pablion-questions/MUI_PCS":
        url =
          "https://github.com/Pablion/pablion-questions/blob/master/src/MUI501-props-changes-style/MUI_PCS.tsx";
        break;
      case "/pablion-questions/":
        url = "https://github.com/Pablion/pablion-questions/";
        break;
      default:
      // do nothing here
    }
    if (url) {
      window.open(url, "_blank", "noopener");
    } else {
      const ERR_NO_PATHNAME =
        "Error on resolving location pathname. Maybe your ENV doesn't have a window(like node.js).";
      alert(ERR_NO_PATHNAME);
      throw Error(ERR_NO_PATHNAME);
    }
  }

  return (
    <BrowserRouter basename="/pablion-questions">
      <StyledEngineProvider injectFirst>
        <ThemeProvider theme={theme}>
          <AppWarp>
            <Header>
              <Box>
                MUI:
                <ButtonGroup>
                  <Button component={Link} to="/MUI_PCS">
                    props changes style
                  </Button>
                </ButtonGroup>
              </Box>
              <Box>
                Source code of this page:
                <ButtonGroup>
                  <Button onClick={handleSourceCodeClick}>
                    <GitHub fontSize="medium" />
                  </Button>
                </ButtonGroup>
              </Box>
            </Header>
            <Switch>
              <Route exact path="/">
                <Typography variant="h2">
                  Select from samples above to show content
                </Typography>
              </Route>
              <Route path="/MUI_PCS" children={<MUI_PCS />} />
            </Switch>
            <Footer />
          </AppWarp>
        </ThemeProvider>
      </StyledEngineProvider>
    </BrowserRouter>
  );
}

export default App;
